import React, { useEffect, useState, useRef } from "react";

import { getStore, updStore, gen16CodeId, parseDate, fetchPostH, getUrlServerSide, ObjectKeys } from "../../../stores/Utils";

import "./dashboard.css"
import {  UpdEmployee_Cmpt,   ViewSubMayor_Cmpt } from "..";
import { OpenModal } from "../../../components/DialogHRM";
import { getall_employee, sortEmployees, delete_employee, get_filter_paystub } from "../services/server_fetching";
import { SvgIcon_Cmpt } from "../../../components";
import DeleteAlert from "./deleteAlert";
import PaystubEmployee from "./PaystubEmployee";

const Svg = SvgIcon_Cmpt();

const UpdEmployee = UpdEmployee_Cmpt();

const ViewSubMayor = ViewSubMayor_Cmpt()

const EmployeeItem  = ({ data, elmId, handleRefreshAll }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);

	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			
			// let frm2 = {"firstName":"Belisario","lastName":"Gonzalez Arevalo","genre":"Male","dateOfBirth":"06/02/1976","placeOfBirth":{"city":"Holguin","state":"Holguin"},"currentAddress":{"No":"4307","street":"Estate Dr","city":"Louisville","state":"Kentucky","zipcode":"40216"},"dob":202536000000}
			//updStore(frmNm, frm2);
			set_obs(gen16CodeId())
		}
	});

	
	const updObs =  () => {
		set_obs(gen16CodeId())
	}

	

	const refreshAccs =  () => {
		getall_employee(updObs)
	}

	const update = async () => {
		updStore("upd_employee", data);
		updStore("upd_employee_bck", JSON.parse(JSON.stringify(data)));
		let modaldata = {};
		modaldata["zIndex"] = 450;
		modaldata["height"] = "80vh";
		// data['observeResize']=true;
		modaldata["props"] = { minHeight: "1vh", overlay: true };
		modaldata["content"] = <UpdEmployee confirm={refreshAccs} />;
		OpenModal(modaldata);
	}

	
	const openSubMayorModal =  (sba) => {
		updStore("employee_sub_mayor", data);
		let modaldata = {};
		modaldata["zIndex"] = 450;
		modaldata["height"] = "80vh";
		// data['observeResize']=true;
		modaldata["props"] = { minHeight: "1vh", overlay: true };
		modaldata["content"] = 
			<ViewSubMayor confirm={refreshAccs} item={data}/>
		OpenModal(modaldata);
	}


	const openPayViewModal =  (sba) => {
		updStore("employee_sub_mayor", data);
		let modaldata = {};
		modaldata["zIndex"] = 450;
		modaldata["height"] = "80vh";
		// data['observeResize']=true;
		modaldata["props"] = { minHeight: "1vh", overlay: true };
		modaldata["content"] = 
			<PaystubEmployee  item={data}/>
		OpenModal(modaldata);
	}


	const openSubMayor =  (sba) => {
		get_filter_paystub( data?.employeeId, openSubMayorModal)
	}


	const deleteComprobante =  () => {
		delete_employee(data?.employeeId, handleRefreshAll);
	}
	

	const OpenDeleteAlert =  () => {
		let lastTimeCallModal = getStore("lastTimeCallModal") || 0;
		if(Date.now() > lastTimeCallModal){
			updStore("lastTimeCallModal", Date.now()+320);
			let modaldata = {};
			modaldata["zIndex"] = 450;
			modaldata["height"] = "40vh";
			// data['observeResize']=true;
			modaldata["props"] = { minHeight: "1vh", overlay: true, closeEvent: ()=>{}  };
			modaldata["content"] = <DeleteAlert confirm={deleteComprobante} item={data} />
			OpenModal(modaldata);
		}
	}

	

	let subAcc = sortEmployees( getStore("sub_employee_group")?.[data.employeeId],"subEmployee");
	
	


	return (
		<div className="item_acc">
			<div className="_dsplFlx item_box employee_header">
				<div className="_dsplFlx"  onClick={update}>
					<div className="code" >
						
					</div>
					<div className="name">
						{data?. name}
					</div>
					<div className="unit" >
						{data?.unit} 
					</div>
				</div>
				<div className="flexSpace"/>

				
				<div className=" addSbAcc purple" onClick={openPayViewModal}>
					Make a Payment
				</div>
				<div className=" addSbAcc purple" onClick={()=>openSubMayor(null)}>
					Paychecks
				</div>
				
				<div className="icon" style={{marginLeft: 19}} onClick={OpenDeleteAlert}>
					<Svg name={"outline_delete"} color={"#c62828"}/>
				</div>
			</div>
			
		</div>
	)
}
export default EmployeeItem;