import React, { useEffect, useState, useRef } from "react";



import { getStore, updStore, gen16CodeId, parseDate, fetchPostH, getUrlServerSide, compareJSON, fetchEncryptStream, isNotEmpty, ObjectKeys, parseFloatToDecimal, qryStore, generateComprobanteId } from "../../../stores/Utils";

import { CheckBoxSlide_Cmpt, TextInput_Cmpt, CheckBox_Cmpt, DropDwnPicker_Cmpt, SvgIcon_Cmpt } from "../../../components";


import "./dashboard.css"
import { CloseModal, OpenModal } from "../../../components/DialogHRM";
import { DateCalComponent_Cmpt } from "../../Inventory";
import { CardsHrm, GrayButton } from "./cardshrm";
import { SelectDropAccount_Cmpt } from "../../ComprobanteParametrizados";
import { useNavigate } from "react-router";

const CheckBoxSlide = CheckBoxSlide_Cmpt();
const TextInput = TextInput_Cmpt();

const DropDwnPicker = DropDwnPicker_Cmpt();
const DateCalComponent = DateCalComponent_Cmpt();


const DropInput = SelectDropAccount_Cmpt();

const Svg = SvgIcon_Cmpt();



const frmNm = "paystub_employee";

const PaystubEmployee  = ({data, confirm, item}) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);
	const navigate = useNavigate();


	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			
			set_obs(gen16CodeId())
		}
	});

	const updFrm = (f, v, k) => {
		let frm2 = getStore(frmNm) || {};
		if(k){
			if( !frm2[k]){
				frm2[k] = {};
			}
			frm2[k][f] = v;
		}else{
			frm2[f] = v;
		}
		updStore(frmNm, frm2);
		
		set_obs(gen16CodeId())
	};



	const save  =  ()  => {
	
		if(item.hasW2){
			saveW2()
		}else{
			save1099()
		}
	};


	
	const save1099 = async (f) => {
		let ffm = getStore(frmNm) || {};
		let date = (new Date()).getTime()+800;
		if(ffm["date"]){
			let nn = (new Date(ffm["date"])).getTime();
			date = nn?nn:date;
		}
		ffm["date"] = date; 

		let comprobanteId = await generateComprobanteId(ffm["date"]);

		ffm["comprobanteId"] =comprobanteId;
		ffm["document"] = ffm["document"]?.trim();


		if(!ffm["description"]){
			ffm["description"] = "pago de nomina";
		}

		ffm["employeeId"] = item?.employeeId;
		let comp2save = [];
		
	
		let frmSlr = {...ffm}
		
		frmSlr["account"] = getStore("global_account_params")?.salary;

		let amountSlr = parseFloatToDecimal(ffm["amount"],2);
		frmSlr["type"] = "debit";
		frmSlr["operationId"] = gen16CodeId();
		frmSlr["amount"] =amountSlr*1;
		let bdyq5 = {...getStore(qryStore())?.add_opera};
		bdyq5.params = {
			businessId:  getStore("profileUser")?.businessId
		};
		bdyq5.form=frmSlr;
		comp2save.push(bdyq5)

		let frmBnk = {...ffm}


		frmBnk["account"] = getStore("global_account_params")?.cash_bank.account;
		if(ffm?.futureSlipCheck){
			frmBnk["account"] = getStore("global_account_params")?.futureSlipCheck?.account;
			frmBnk["subAccount"] = getStore("global_account_params")?.futureSlipCheck?.subAccount;
		}
		

		let amountBank = parseFloatToDecimal(ffm["amount"],2);
		frmBnk["type"] = "credit";
		frmBnk["operationId"] = gen16CodeId();
		frmBnk["amount"] =amountBank*1;

		let bdyq6 = {...getStore(qryStore())?.add_opera};
		bdyq6.params = {
			businessId:  getStore("profileUser")?.businessId
		};
		bdyq6.form=frmBnk;
		comp2save.push(bdyq6)


		fetch2Server(comp2save, comprobanteId)


	}


	const saveW2 = async (f) => {

		let ffm = getStore(frmNm) || {};
		let date = (new Date()).getTime()+800;
		if(ffm["date"]){
			let nn = (new Date(ffm["date"])).getTime();
			date = nn?nn:date;
		}
		ffm["date"] = date; 

		let comprobanteId = await generateComprobanteId(ffm["date"]);

		ffm["comprobanteId"] =comprobanteId;
		ffm["document"] = ffm["document"]?.trim();


		if(!ffm["description"]){
			ffm["description"] = "pago de nomina";
		}

		ffm["employeeId"] = item?.employeeId;
		let comp2save = [];
		
	
		let totalC = 0;

		item.w2 && ObjectKeys(item.w2).map(tx=>{
			
			let nnfrm = {...ffm}
			let dataAcc = {...getStore("global_account_params")?.w2_params?.[tx]?.account};

			nnfrm["account"] =  dataAcc["account"];
			nnfrm["subAccount"] = dataAcc["subAccount"];

			let amountT = nnfrm["amount"] * item.w2[tx]?.percent / 100;
			let amount = parseFloatToDecimal(amountT,2)
			nnfrm["type"] = "credit";
			nnfrm["operationId"] = gen16CodeId();
			
			
			
			if(tx === "socialSecurity"){
				let frmSS = {...ffm};
	
				frmSS["account"] = getStore("global_account_params")?.socialsecurity_tax_expenses?.account;
	
				frmSS["type"] = "debit";
				frmSS["amount"] = amount*1;
				frmSS["operationId"] = gen16CodeId();
				totalC += amount*1;

				nnfrm["amount"] =amount*2;
				let bdyq2 = {...getStore(qryStore())?.add_opera};
				bdyq2.params = {
					businessId:  getStore("profileUser")?.businessId
				};
				bdyq2.form=frmSS;
				comp2save.push(bdyq2)
			}
			else if( tx === "medicareTax"){
				let frmSM = {...ffm};
				frmSM["account"] = getStore("global_account_params")?.medicare_tax_expenses?.account;
	
				frmSM["type"] = "debit";
				frmSM["amount"] = amount*1;
				frmSM["operationId"] = gen16CodeId();
				nnfrm["amount"] =amount*2;	
				totalC += amount*1;
				let bdyq2 = {...getStore(qryStore())?.add_opera};
				bdyq2.params = {
					businessId:  getStore("profileUser")?.businessId
				};
				bdyq2.form=frmSM;
				comp2save.push(bdyq2)
			}
			else{
				nnfrm["amount"] =amount*1;
				totalC += amount*1;
			}
			
			let bdyq1 = {...getStore(qryStore())?.add_opera};
			bdyq1.params = {
				businessId:  getStore("profileUser")?.businessId
			};
			bdyq1.form=nnfrm;
			comp2save.push(bdyq1)


		})

		let frmSlr = {...ffm}
		
		frmSlr["account"] = getStore("global_account_params")?.salary;

		let amountSlrt = ffm["amount"];
		let amountSlr = parseFloatToDecimal(amountSlrt,2);
		frmSlr["type"] = "debit";
		frmSlr["operationId"] = gen16CodeId();
		frmSlr["amount"] =amountSlr*1;
		let bdyq5 = {...getStore(qryStore())?.add_opera};
		bdyq5.params = {
			businessId:  getStore("profileUser")?.businessId
		};
		bdyq5.form=frmSlr;
		comp2save.push(bdyq5)

		let frmBnk = {...ffm}

		frmBnk["account"] = getStore("global_account_params")?.cash_bank.account;
		if(ffm?.futureSlipCheck){
			frmBnk["account"] = getStore("global_account_params")?.futureSlipCheck?.account;
			frmBnk["subAccount"] = getStore("global_account_params")?.futureSlipCheck?.subAccount;
		}
		

		let amountTBank = ffm["amount"] - totalC ;
		let amountBank = parseFloatToDecimal(amountTBank,2);
		frmBnk["type"] = "credit";
		frmBnk["operationId"] = gen16CodeId();
		frmBnk["amount"] =amountBank*1;

		let bdyq6 = {...getStore(qryStore())?.add_opera};
		bdyq6.params = {
			businessId:  getStore("profileUser")?.businessId
		};
		bdyq6.form=frmBnk;
		comp2save.push(bdyq6)





		// console.log(comp2save)

		fetch2Server(comp2save, comprobanteId)

		
	
	
	};

	




	const fetch2Server = async (comp2save, comprobanteId) => {

		const promises = comp2save.map(async (qry2Sv) => {
			const res = fetchEncryptStream(qry2Sv);
			const td = await res;
			if (td) {
				//console.log(td)
			}
        })      
        await Promise.all(promises);

		
		setTimeout(() => {
			updStore(frmNm, null)
			data?.modalID && CloseModal({id:data?.modalID});
			navigate({
				pathname: "/accounting-ledger" ,
				search: `?cId=${comprobanteId}`
			});
		}, 400);
	}
	
	return (
		<div
		style={{
			maxHeight: "80vh",
			width: "690px",
			overflow: "auto",
			background: "var(--hrm_palette-general-background-b)",
			borderRadius: 13,
			padding: "10px 19px 25px"
		}}
		>
			<div className="_dsplFlx ">
				<p className="form_title">
					Registrar Pago Nomina
				</p>
				<div className="flexSpace"/>
				<div className="employee_name">
						{item?.name}
				</div>
			</div>

			<div className="_dsplFlx " style={{margin:"10px 10px 10px 0"}}>
				<div className="flexSpace"/>
				<div style={{margin:"14px 10px 0px 0"}}>
					<DateCalComponent  
						init={getStore(frmNm)?.["date"]} 
						label={"Fecha"}
						updDate={(e)=>updFrm("date",e)}
					/>
				</div>
				<div className="flexSpace"/>
				<TextInput  
					init={getStore(frmNm)?.["document"]} 
					label={"Document"} 
					width={220} 
					updChanges={(e)=>updFrm("document",e)}
				/>
				<div className="flexSpace"/>
				<TextInput  
					init={getStore(frmNm)?.["amount"] } 
					label={"Salario"} 
					width={120} 
					type={"number"}
					updChanges={(e)=>updFrm("amount",e)}
				/>
				<div className="flexSpace"/>
				
				
			</div>

			<div className="_dsplFlx " style={{margin:"10px 10px 10px 0"}}>
				<TextInput  
					init={getStore(frmNm)?.["description"]} 
					label={"Description"} 
					width={420} 
					updChanges={(e)=>updFrm("description",e)}
				/>
				<div className="flexSpace"/>
				<div className="_dsplFlx " style={{marginTop: 48}}>
					<span className={`checklabel`}>
						{"Cheque a Futuro"}
					</span>
					<CheckBoxSlide
						initvalue={getStore(frmNm)?.["futureSlipCheck"]}
						keyCode={57}
						updChange={(e)=>updFrm("futureSlipCheck",e)}
					/>
				</div>
			</div>
		
			
			{item?.["hasW2"] ?
			<div style={{marginTop:19}}>
				<CardTaxW2 item={item?.w2}  amount={getStore(frmNm)?.["amount"]|| 0}/>
			</div>
			:null}

			<div className="_dsplFlx ">
				<div className="flexSpace"/>
				
				<div style={{margin:"38px 10px 0px 0"}}>
					<GrayButton
						clickHandler={save}
						label={"Guardar"}
						
					/>
				</div>
				
			</div>

			
		</div>
	);
};

export default PaystubEmployee;




export const CardTaxW2  = ({ item, amount }) => {
	
 
	return (
		<CardsHrm >
			<div className="iMTYiw hiKoiM">{"W2 Tax deduction"}</div>
			<div className="_dsplFlx w_100prc" style={{marginTop:19}} />
			
			<TaxDecuctionItem 
				label={"Federal Tax"}
				// percentDefault={14.5}
				keyFld={"federalTax"}
				item={item}
				amount={amount}
			/>
			<TaxDecuctionItem 
				label={"Social Security"}
				// percentDefault={6.2}
				keyFld={"socialSecurity"}
				item={item}
				amount={amount}
			/>
			<TaxDecuctionItem 
				label={"Medicare"}
				// percentDefault={1.45}
				keyFld={"medicareTax"}
				item={item}
				amount={amount}
			/>
			<TaxDecuctionItem 
				label={"Estate"}
				// percentDefault={4.5}
				keyFld={"stateTax"}
				item={item}
				amount={amount}
			/>
			<TaxDecuctionItem
				label={"City"}
				// percentDefault={2.2}
				keyFld={"cityTax"}
				item={item}
				amount={amount}
			/>
		</CardsHrm>
	)
}


export const TaxDecuctionItem  = ({ label, item,  keyFld, percentDefault, amount }) => {

	let percentLbl =   item?.[keyFld]?.percent || percentDefault;


	let dataAcc = {...getStore("global_account_params")?.w2_params?.[keyFld]?.account};


	let hasError = !dataAcc?.account;


	
	return (
		<div className="_dsplFlx w_100prc">
			<div className="icon_revision" >
				<Svg 
					name={hasError ? "alert":"check_circle"} 
					color={hasError ? "var(--hrm_palette-selected-nav-text)":"var(--hrm_palette-success-fg)"}
				/>
			</div>
			
			<div className="igLyC jyoObE pddBtm-12">
				<div className="txfnt_lfal" >
				{label}
				</div>
			</div>
		
			<div className="igLyC jyoObE" >{percentLbl+"%"}</div>
			<div className="igLyC jyoObE" >{(percentLbl/100*amount).toFixed(2)}</div>
			
		</div>
	)
}








const AddExtraExpenses = ({data, confirm, item}) => {
	const [obs, set_obs] = useState(0);
	const [acc, setAcc] = useState({});
	let account = acc?.account || item?.account; 
	let sub_account = acc?.subAccount || item?.subAccount; 

	let accountD = getStore("account_ids")?.[account];
	let subaccountD = getStore("sub_account_ids")?.[sub_account];


	const addExtraAccount2List = () => {
		confirm && confirm(acc);
		CloseModal({id:data.modalID})
	}

	const handlePick = (acc,subacc) => {
		let nn= {
			"account":acc,
			"subAccount":subacc
		}
		setAcc(nn);
		set_obs(gen16CodeId())
	}


	
	
	const clear = () => {
		setAcc(null);
		set_obs(gen16CodeId())
	};

	return (
		<div style={{
			maxHeight: "80vh",
			minHeight: "60vh",
			width: "560px",
			overflow: "auto",
			background: "#fff",
			borderRadius: 13,
			padding: "10px 19px"
		}}>
			<div className="_dsplFlx ">
				<p className="form_title">
					Associar Cuenta
				</p>
				<div className="flexSpace"/>
				
			</div>
		{account ? 
			<div className="_dsplFlx " style={{margin: "18px 0"}}>
				<div className=" acc_dtls">
					<div className="_dsplFlx"   onClick={clear}>
						<div className="code" style={{color: "#646cff"}}>
							{accountD?.code} 
						</div>
						<div className="name">
							{accountD?.name}
						</div>
					</div>
					{subaccountD ?
						<div className="_dsplFlx"  style={{margin: "10px 0 0 18px"}}>
							<div className="code" >
								{subaccountD?.subAccount} 
							</div>
							<div className="name">
								{subaccountD?. subAccountName}
							</div>
						</div>
						:null}
					<div className="flexSpace"/>
				</div>
			</div> :
			<DropInput
				width={420}
				handleClick={handlePick}
			/>
			}
			<div className="card">
				<button onClick={addExtraAccount2List}>
					Associar
				</button>
			</div>
			</div>
	)
}



